import React from "react";
import { useSelector } from "react-redux";
import Navbar from "./Navbar";
// import { removeFromCart, increaseQty, decreaseQty } from "./cartSlice";

const Cart = () => {
  const { cartItem } = useSelector((state) => state.products);
  const totalCost = cartItem.reduce(
    (acc, item) => acc + item.price * item.qty,
    0
  );
  // const dispatch = useDispatch();

  const handleRemove = (itemId) => {
    dispatch(removeFromCart(itemId));
  };

  const handleIncrease = (itemId) => {
    dispatch(increaseQty(itemId));
  };

  const handleDecrease = (itemId, qty) => {
    if (qty > 1) {
      // dispatch(decreaseQty(itemId));
    }
  };

  return (
    <>
      <Navbar />
      <div className="md:mx-28 mt-5">
        <h2 className="text-2xl font-medium mb-6 uppercase">Shopping Cart</h2>
        <div className="flex items-center">
          {cartItem.map((item) => (
            <CardItem item={item} />
          ))}
        </div>
        <div className="flex justify-end mt-6">
          <p className="font-medium text-lg">Total: ${totalCost}</p>
        </div>
      </div>
    </>
  );
};

// card

const CardItem = ({ item }) => {
  return (
    <div className="w-full md:w-1/2 lg:w-1/3">
      <div className="bg-white shadow p-6">
        <img
          src={item.img}
          alt={item.heading}
          className="h-32 w-full object-cover"
        />
        <h3 className="text-lg font-medium mt-4">{item.heading}</h3>
        <p className="text-gray-600 mt-2">${item.price}</p>
        <div className="flex items-center mt-4">
          <button
            onClick={() => handleDecrease(item.id, item.qty)}
            className="bg-gray-200 px-2 py-1 rounded-full hover:bg-gray-400"
          >
            -
          </button>
          <p className="px-2">{item.qty}</p>
          <button
            onClick={() => handleIncrease(item.id)}
            className="bg-gray-200 px-2 py-1 rounded-full hover:bg-gray-400"
          >
            +
          </button>
        </div>
        <p className="text-gray-600 mt-2">Total: ${item.price * item.qty}</p>
        <button
          onClick={() => handleRemove(item.id)}
          className="mt-4 text-red-600 hover:underline"
        >
          Remove
        </button>
      </div>
    </div>
  );
};

export default Cart;
